.modal-feedback-wrapper {
  display: flex;
  justify-content: flex-start;
  padding: 16px 16px;
  width: 320px;
  position: fixed;
  background-color: #ffffff;
  height: 100%;
  cursor: pointer;
  overflow-y: auto;
  z-index: 101;
  transition: transform 300ms;
  padding-top: 24px;
  right: 0;
  top: 0;
  bottom: 0;
  transform: translateX(360px);
}

.modal-feedback-wrapper.open {
  transform: translateX(0);
  width: 100%;
}

.modal-feedback {
  display: flex;
  flex-direction: column;
  padding-right: 16px;
}

.modal-feedback__title {
  font-size: 24px;
  line-height: 32px;
  position: relative;
  margin-top: 40px;
  font-family: TT lakes bold;
}

.modal-feedback__forms {
  display: flex;
  flex-direction: column;
  gap: 16px;
  font-size: 16px;
}

.modal-feedback__data {
  border: 1px solid #eaeaea;
  border-radius: 8px;
  width: 288px;
  height: 48px;
  color: #b5b6bc;
  padding-left: 16px;
  line-height: 24px;
}

.modal-feedback__msg {
  height: 118px;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  padding: 16px 12px;
  font-size: 16px;
  width: 288px;
}

.agreement {
  margin-top: 16px;
  width: 288px;
}

.agreement__link {
  font-size: 14px;
  line-height: 16px;
  color: red;
  text-decoration: none;
}

.submit {
  width: 136px;
  height: 32px;
  border-radius: 15px;
  border: none;
  background-color: #ff3e79;
  font-size: 12px;
  line-height: 24px;
  color: #ffffff;
  padding: 16px;
  margin-top: 23px;
  align-self: flex-end;
  font-family: TT lakes bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.submit::after {
  content: url(./static/littleArrow.svg);
}

.modal-feedback__title::before {
  content: url('/img/highlight\ \(2\).svg');
  width: 4px;
  position: absolute;
  left: -16px;
}

@media (min-device-width: 768px) {
  .modal-feedback-wrapper.open {
    width: auto;
  }
}

@media (min-device-width: 1420px) {
  .modal-feedback__data {
    width: 376px;
  }

  .modal-feedback__msg {
    width: 376px;
  }
}
