.repairTechnique_container {
  border-top: 1px solid #d9fff5;
  padding-left: 16px;
  margin-top: 24px;
}

.repairTech-title {
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: #7e7e82;
  background: linear-gradient(to right, #f8f8f8, #ffffff);
  height: 56px;
  padding: 16px 0px;
}

a {
  text-decoration: none;
  color: #1b1c21;
}

.tech_list {
  display: flex;
  gap: 16px;
  margin-top: 16px;
}

.tech_list-link {
  border: 1px solid #eaeaea;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  width: 240px !important;
  height: 160px;
  padding: 16px;
}

.tech_list-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #ff3e79;
  background-color: white;
  align-self: flex-end;
}

.tech_list-button::before {
  content: url(./static/pinkk.svg);
}

@media (min-device-width: 768px) {
  .repairTechnique_container {
    padding: 24px;
  }

  .tech_list {
    padding-top: 24px;
  }
}

@media (min-device-width: 1120px) {
  .tech_list {
    gap: 32px;
  }
}
