.prices_container {
  border-top: 1px solid #d9fff5;
  padding-left: 16px;
  margin-top: 24px;
}

.prices-title {
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: #7e7e82;
  background: linear-gradient(to right, #f8f8f8, #ffffff);
  height: 56px;
  padding: 16px 0px;
}

.price_list {
  display: flex;
  gap: 16px;
  margin-top: 16px;
  padding-left: 8px;
}

.price-info {
  font-size: 14px;
  color: #1b1c21;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 260px !important;
  height: 200px;
  padding: 24px;
  position: relative;
  border-radius: 6px;
  background-color: #ffffff;
}

.prices_list-button {
  width: 120px;
  height: 32px;
  border-radius: 15px;
  border: none;
  background-color: #ff3e79;
  font-size: 12px;
  line-height: 24px;
  color: #ffffff;
  position: absolute;
  bottom: 15px;
  right: 15px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  font-family: TT lakes bold;
}

.prices_list-button::after {
  content: url(./static/littleArrow.svg);
}

span {
  color: #7e7e82;
}

.repair-services {
  display: flex;
  flex-direction: column;
}

.price {
  display: flex;
  flex-direction: column;
}

.deadline {
  display: flex;
  flex-direction: column;
}

@media (min-device-width: 768px) {
  .prices_container {
    padding: 0px 24px;
    background-color: #f8f8f8;
  }

  .prices_container_swiper {
    padding-top: 31px;
  }

  .price_list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 0px;
    margin-top: 31px;
    background-color: #ffffff;
  }

  .price-info {
    min-width: 100%;
    flex-direction: row;
    align-items: center;
    position: relative;
    height: 64px;

    &:not(:nth-last-child(1))::before {
      content: '';
      position: absolute;
      height: 1px;
      background-color: #eaeaea;
      bottom: 0;
      left: 16px;
      right: 16px;
    }
  }

  .headers {
    display: none;
  }

  .main_headers {
    position: absolute;
    top: -25px;
  }

  .prices_list-button {
    position: static;
  }

  .repair-services {
    width: 348px;
  }

  .deadline {
    width: 81px;
  }

  .price {
    width: 74px;
  }
}
