.footer {
  margin-top: 40px;
  padding-top: 16px;
  border-top: 1px solid #d9fff5;
}

.confidentiality {
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  text-align: center;
  gap: 16px;
  padding-bottom: 19px;
  display: flex;
  flex-direction: column;
}

@media (min-device-width: 768px) {
  .confidentiality {
    flex-direction: row;
    justify-content: space-between;
    padding: 24px;
  }

  .confidentiality_One {
    text-align: start;
    width: 238px;
  }

  .confidentiality_Two {
    white-space: nowrap;
    width: 220px;
  }

  .confidentiality_Three {
    text-align: end;
    width: 210px;
  }
}

@media (min-device-width: 1120px) {
  .confidentiality_Three {
    width: 330px;
  }
}
