* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header-nav-menu {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #d9fff5;
  background-color: #ffffff;
  height: 88px;
  padding: 0 16px;
}
.header-nav-menu_left {
  display: flex;
  gap: 16px;
  padding: 24px 0px 24px 0px;
}

.header-nav-menu_right {
  display: flex;
  gap: 16px;
  padding: 24px 0px 24px 0px;
}

.hidden-menu-buttons {
  display: none;
  margin-right: 20px;
  justify-content: center;
  align-items: center;
  gap: 16px;
}

.btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #ff3e79;
}

.burger-menu {
  background-image: url(./static/burger.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.repair {
  background-image: url(./static/repair.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.search {
  background-image: url(./static/search.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.header-nav-menu_right::before {
  content: url(./static/divider.png);
  position: absolute;
  top: 30px;
  left: 185px;
}

.phone {
  background-image: url(./static/phone.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.msg {
  background-image: url(./static/phone.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.contacts {
  background-image: url(./static/contacts.svg);
  background-repeat: no-repeat;
  background-position: center;
}

@media (min-device-width: 768px) {
  .hidden-menu-buttons {
    display: flex;
    align-items: start;
  }

  .hidden-menu-buttons::after {
    content: url(./static/divider.png);
    position: absolute;
    right: 130px;
    top: 30px;
  }

  .header-nav-menu_left {
    gap: 32px;
  }

  .header-nav-menu_right::before {
    left: 70px;
  }
}

@media (min-device-width: 1420px) {
  .header-nav-menu {
    display: none;
  }
}
