.repairBrands_container {
  border-top: 1px solid #d9fff5;
  padding-left: 16px;
  margin-top: 24px;
}

.repairBrands-title {
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: #7e7e82;
  background: linear-gradient(to right, #f8f8f8, #ffffff);
  height: 56px;
  padding: 16px 0px;
}

.brand_list {
  display: flex;
  column-gap: 24px;
  row-gap: 16px;
  margin-top: 16px;
}

.brand_list-link {
  border: 1px solid #eaeaea;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px !important;
  height: 72px;
  padding: 20px 16px;
}

.brand_list-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #ff3e79;
  background-color: white;
}

.brand_list-button::before {
  content: url(./static/pinkk.svg);
}

.show_all {
  display: none;
}

.swiper-pagination {
  position: static;
  --swiper-pagination-top: auto;
  --swiper-pagination-bullet-width: 12px;
  --swiper-pagination-bullet-height: 12px;
  --swiper-pagination-color: #b5b6bc;
  --swiper-pagination-bullet-inactive-color: dddddd;
  margin-top: 16px;
}

@media (min-device-width: 768px) {
  .brand_list {
    flex-wrap: wrap;
    width: 100%;
    column-gap: 24px;
    padding-top: 20px;
  }

  .stealth {
    display: none;
  }

  .swiper-slide {
    width: 224px !important;
  }

  .show_all {
    display: inline;
    border: none;
    color: #1b1c21;
    font-size: 16px;
    line-height: 24px;
    padding-left: 7px;
    margin-top: 24px;
    background: transparent;
    width: 150px;
    font-family: TT lakes medium;
  }

  .show_all::before {
    content: url(./static/blue-arrows_down.svg);
    position: absolute;
    left: 22px;
  }
}

@media (min-device-width: 1120px) {
  .stealth {
    display: none;
  }

  .brand_list {
    column-gap: 32px;
  }

  .swiper-slide {
    width: 240px !important;
  }

  .acer {
    display: flex;
  }

  .sony {
    display: flex;
  }
}

@media (min-device-width: 1420px) {
  .show_all {
    position: relative;
  }
  .show_all::before {
    left: 0px;
    top: 0px;
  }
}
