.prices_info {
  padding: 0px 16px 16px 16px;
  position: relative;
  width: 288px;
  margin-top: 16px;
}

.NDS {
  font-size: 14px;
  line-height: 20px;
  color: #1b1c21;
}

.offer {
  display: inline-block;
  font-size: 16px;
  line-height: 24px;
  color: #1b1c21;
  margin-top: 16px;
}

.offer::after {
  content: url(./static/pinkk.svg);
  position: absolute;
  left: 130px;
  bottom: 14px;
}

@media (min-device-width: 768px) {
  .prices_info {
    width: 451px;
    padding: 16px 24px 24px 24px;
  }

  .offer::after {
    left: 332px;
    bottom: 22px;
  }
}
