.modal-menu {
  max-width: 360px;
  min-width: 320px;
  width: 100%;
  position: fixed;
  background-color: #ffffff;
  height: 100%;
  overflow-y: auto;
  z-index: 100;
  transition: transform 300ms;
  display: flex;
  flex-direction: column;
  left: 0;
  transform: translateX(-360px);
  padding: 24px 16px;
}

.modal-menu.open {
  display: flex;
  transform: translateX(0);
}

.modal-menu__nav {
  display: flex;
  justify-content: space-between;
}

.modal-menu_left {
  display: flex;
  gap: 16px;
}

.modal-menu_right {
  display: flex;
}

.btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #ff3e79;
}

.cross {
  background-image: url(./static/cross.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.search {
  background-image: url(./static/search.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.phone {
  background-image: url(./static/phone.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.msg {
  background-image: url(./static/msg.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.contacts {
  background-image: url(./static/contacts.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.modal-menu__content {
  display: flex;
  flex-direction: column;

  color: #1b1c21;
}

.modal-menu__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
  list-style-type: none;
  margin-top: 46px;
  position: relative;
}

.modal-menu__border {
  border: none;
  background-color: white;
  color: #1b1c21;
  font-size: 16px;
  line-height: 24px;
  font-family: TT Lakes medium;
}

.activated {
  color: #7e7e82;
}

.activated::before {
  content: url(./static/palka.svg);
  width: 4px;
  height: 32px;
  position: absolute;
  left: -23px;
  top: -10px;
}

.modal-menu__footer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: auto;
}

.modal-menu__footer-nav {
  display: flex;
  gap: 16px;
}

.modal-menu__footer-info {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.modal-menu__email {
  cursor: pointer;
  color: black;
  text-decoration: none;
}

.modal-menu__phone {
  cursor: pointer;
  font-family: TT Lakes bold;
  font-size: 24px;
  color: black;
  text-decoration: none;
}

.modal-menu__footer-lang {
  margin-top: auto;
  margin-bottom: 32px;
}

.radio {
  appearance: none;
}

.lang-active {
  color: #7e7e82;
}

label {
  cursor: pointer;
}

@media (min-device-width: 768px) {
  .modal-menu__footer-lang {
    margin-top: 40px;
    margin-bottom: 32px;
  }
}

@media (min-device-width: 1420px) {
  .modal-menu {
    transform: translateX(0);
    top: 0;
    bottom: 0;
    width: 330px;
  }

  .main {
    padding-left: 330px;
  }

  .cross {
    visibility: hidden;
  }
}
