* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  width: 100%;
  height: 100%;
  font-family: TT Lakes medium;
  background-color: #f8f8f8;
  overflow-x: hidden; }

.modal-overlay {
  z-index: 10;
  background-color: #f8f8f8;
  position: fixed;
  inset: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 200ms; }

.modal-overlay.open {
  visibility: visible;
  opacity: 0.7; }

.blur {
  -webkit-filter: blur(8px);
          filter: blur(8px); }
