.services-container {
  display: flex;
  flex-direction: column;
  padding-top: 24px;
  background-color: #f8f8f8;
  padding: 0 16px;
  margin-top: 24px;
}

.services-title {
  font-size: 24px;
  line-height: 40px;
  color: #1b1c21;
  font-family: TT lakes bold;
  position: relative;
}

.services-title::before {
  content: url(./static/palka.svg);
  width: 4px;
  height: 40px;
  position: absolute;
  left: -15px;

  @media (min-device-width: 768px) {
    left: -23px;
  }
}

.title-buttons {
  display: none;
  align-items: center;
  gap: 15px;
  margin-right: 32px;
}

.services {
  display: flex;
  align-items: center;
  gap: 16px;
  list-style-type: none;
  width: 100%;
  font-size: 16px;
  line-height: 24px;
  overflow-x: auto;
  flex-wrap: nowrap;
  white-space: nowrap;
  color: #7e7e82;
  margin-top: 24px;
}

.list-item {
  padding: 8px 9px 8px 16px;
}

.button-border {
  border: none;
  background-color: #f8f8f8;
  font-family: TT lakes medium;
}

.active {
  border: 2px solid #b8ffec;
  border-radius: 6px;
  background-color: #f8f8f8;
}

.services-description {
  margin-top: 24px;
}

.description {
  font-size: 14px;
  line-height: 18px;
  color: #1b1c21;
}

.description-two {
  display: none;
  font-size: 14px;
  line-height: 18px;
  margin-top: 24px;
  color: #1b1c21;
}

.read-more {
  display: flex;
  white-space: nowrap;
  border: none;
  width: 100px;
  height: 24px;
  font-size: 16px;
  line-height: 24px;
  color: #1b1c21;
  margin: 16px 0px 16px 28px;
  background: transparent;
  font-family: TT lakes medium;
}

.read-more::before {
  content: url(./static/elka.svg);
  width: 24px;
  height: 24px;
  position: absolute;
  left: 15px;
}

.big-image {
  width: 320px;
  margin-left: -16px;
}

@media (min-device-width: 768px) {
  .services-container {
    padding: 24px;
  }
  .services-description {
    display: flex;
    justify-content: space-between;
    margin-top: 32px;
  }

  .services-desctiption-wrapper {
    display: flex;
    flex-direction: column;
    width: 312px;
  }

  .description-two {
    display: block;
  }

  .big-image {
    width: 360px;
    height: 208px;
  }
  .read-more {
    display: none;
  }
}

@media (min-device-width: 1120px) {
  .services-title-wrapper {
    display: flex;
    justify-content: space-between;
  }

  .big-image {
    width: 532px;
    height: 307px;
  }

  .services-desctiption-wrapper {
    width: 490px;
  }

  .read-more {
    display: none;
  }

  .read-more::before {
    left: 22px;
  }
}

@media (min-device-width: 1420px) {
  .services-title::before {
    left: -24px;
  }
  .title-buttons {
    display: flex;
    margin-right: 0px;
  }
}
